<template>
  <div class="panel panel-default action">
    <div class="panel-heading action-heading">
      <input placeholder="Action Title" v-model="actionName" class="title-input action-name" />
      <button @click="removeSelf()">
          <i class="fa fa-times"></i>
      </button>
    </div>


    <div class="panel-body">
      <div class="dependencies sexp-box">
        <div class="sexp-label">
          <label>When</label>
          <button class="btn-icon add-sexp" v-on:click="addCondition()">
            <i class="fa fa-plus"></i>
          </button>
        </div>
        <div class="sexp">
          <div class="placeholder" v-if="props.conditions.length === 0">
            Step Starts
          </div>
          <div v-else v-for="(condition, index) in props.conditions" class="tree">
            <sexp @input="setTree('conditions', index, arguments[0])"
                  :value="condition.tree"
                  :action="action"
                  :focused="true"
                  :scope="scope"
                  :key="index" />
          </div>
        </div>
      </div>

      <div class="package sexp-box">
        <div class="sexp-label">
          <label>Then Send</label>
          <button class="btn-icon add-sexp" v-on:click="addItem()">
            <i class="fa fa-plus"></i>
          </button>
        </div>
        <div>
          <div v-for="(item, index) in props.package" class="tree">
            <sexp @input="setTree('package', index, arguments[0])"
              :value="item.tree"
              :focused="true"
              :action="action"
              :scope="scope"
              :key="index" />
          </div>
        </div>

      </div>

      <div class="recipients sexp-box">
        <div class="sexp-label">
          <label>To</label>
          <button class="btn-icon add-sexp" v-on:click="addRecipient()">
            <i class="fa fa-plus"></i>
          </button>
        </div>
        <div class="checkbox">
          <label>
            <input v-model="multiplePackages" type="checkbox" class="checkbox" /> Per Member
          </label>
        </div>
        <div>
          <div v-for="(recipient, index) in props.recipients" class="tree">
            <sexp @input="setTree('recipients', index, arguments[0])"
              :value="recipient.tree"
              :action="action"
              :focused="true"
              :scope="scope"
              :key="index" />
          </div>
        </div>

      </div>

    </div>

    <div class="panel-footer">
      <div class="drag-source-container results">
        <div class="sexp-label"><label>Results</label></div>
        <div class="drag-source">
          <div v-for="(result, index) in results" class="tree">
            <sexp
              :value="result"
              :action="action"
              :focused="false"
              :scope="scope"
              :key="index" />
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import {lensProp, lensPath, append, remove, over, set} from 'ramda'
import {mapMutations} from 'vuex'
import Panel from './Panel'
import Sexp from './Sexp'
import {types} from '../sexp'
import {checkbox, select} from 'vue-strap'

const appendToChild = (prop, value, object) => {
  return over(lensProp(prop), append(value), object)
}

export default {
  props: {
    action: {
      type: Object,
      required: 'true'
    },

    step: {
      type: Object,
      required: 'true'
    }
  },

  components: {
    Panel,
    Sexp,
    checkbox,
    vSelect: select
  },

  created () {
    if (this.action.props == null) {
      this.setProps({recipients: [], package: [], conditions: []})
    }
  },

  data () {
    return {
      actionName: this.action.name,
      multiplePackages: this.action.multiplePackages
    }
  },

  watch: {
    actionName (newName) {
      this.setActionName({stepId: this.step.id, actionId: this.action.id, name: newName})
    },

    multiplePackages (v) {
      this.setActionMultiplePackages({stepId: this.step.id, actionId: this.action.id, multiplePackages: v})
    }
  },

  computed: {
    props () {
      return this.action.props
    },

    results () {
      return this.action.props.results
    },

    scope () {
      return this.$store.state.scope
    }
  },

  methods: {
    ...mapMutations([
      'setActionName',
      'setActionProps',
      'setActionType',
      'setActionMultiplePackages',
      'removeAction'
    ]),

    removeSelf () {
      this.removeAction({stepId: this.step.id, actionId: this.action.id})
    },

    setSelfType (e) {
      this.setActionType({stepId: this.step.id, actionId: this.action.id, type: e.target.value})
    },

    setProps (newProps) {
      this.setActionProps({stepId: this.step.id, actionId: this.action.id, props: newProps})
    },

    setTree (type, index, newTree) {
      if (newTree == null) {
        this.setProps(over(lensProp(type), remove(index, 1), this.action.props))
        return
      }

      this.setProps(set(lensPath([type, index, 'tree']), newTree, this.action.props))
    },

    addItem () {
      this.setProps(appendToChild(
        'package',
        {tree: {type: 'sexp-hole', valueType: types.request}},
        this.action.props
      ))
    },

    addCondition () {
      this.setProps(appendToChild(
        'conditions',
        {tree: {type: 'sexp-hole', valueType: types.dependency}},
        this.action.props
      ))
    },

    addRecipient () {
      this.setProps(appendToChild(
        'recipients',
        {tree: {type: 'sexp-hole', valueType: types.reciever}},
        this.action.props
      ))
    }
  }
}
</script>

<style scoped>
  .select {
    width: 100%;
  }

  .action-name {
    font-size: 1em;
  }

  .word {
    margin-right: 8px;
  }

  .action-heading {
    display: flex;
  }

  .action-heading > input {
    flex: 1;
  }

  .placeholder {
    opacity: 0.5;
  }

  .action-heading > button {
    background-color: transparent;
    border: none;
    outline: none;
  }

  .tree {
    margin-bottom: 16px;
  }

  .action {
    width: calc(33% - 16px);
  }
</style>



