<template>
  <div :class="{'source-select': true, 'rest': isRest}">
    <div v-show="searchQuery.length === 0" class="placeholder">{{ value.valueType }}</div>
    <div
      class="search"
      contenteditable
      v-focus="localFocused"
      @focus="localFocused = true"
      @blur="localFocused = false"
      @input="search($event.target.textContent)"
      v-on:keydown="receiveKeys"
      ref="input"
      type="text">
    </div>
    <ul v-if="searchOptions.length > 0" class="search-options">
      <li v-for="(option, index) in searchOptions"
          @mouseenter="optionHover(index)"
          @click="optionClick(index)"
          :class="{selected: searchSelect === index}">
          {{option.label}}
      </li>
    </ul>
  </div>
</template>

<script>
  import {map, filter, pipe, prop, flatten} from 'ramda'
  import {codes} from 'keycode'
  import Vue from 'vue'
  import {atoms, typeMatches} from '../sexp'

  const keyBehaviour = {
    [codes.enter]: (ctx) => {
      ctx.selectCurrent()
    },

    [codes.up]: (ctx) => {
      if (ctx.searchSelect !== 0) {
        ctx.searchSelect--
      }
    },

    [codes.down]: (ctx) => {
      if (ctx.searchSelect + 1 !== ctx.searchOptions.length) {
        ctx.searchSelect++
      }
    },

    [codes.backspace]: (ctx) => {
      if (ctx.searchQuery.length === 0) {
        ctx.$emit('input', null)
      }
    }
  }

  export default Vue.component('sexp-hole', {
    props: {
      value: {
        type: Object,
        required: true
      },

      scope: {
        type: Array,
        required: true
      },

      action: {
        type: Object,
        required: true
      },

      focused: Boolean
    },

    created () {
      this.setOptions()

      if (this.localFocused) {
        this.focus()
      }
    },

    data () {
      return {
        localFocused: this.focused,
        searchQuery: '',
        searchOptions: [],
        searchSelect: 0,
        options: []
      }
    },

    computed: {
      isRest () {
        return this.value.valueType && this.value.valueType.rest
      }
    },

    watch: {
      localFocused (value) {
        if (value) {
          this.focus()
        } else {
          setTimeout(() => !this.localFocused && this.blur(), 200)
        }
      },

      focused (value) {
        this.localFocused = value
      },

      searchQuery (newValue) {
        this.$refs.input.textContent = newValue
      },

      scope (newScope) {
        this.setOptions(newScope)
      }
    },

    methods: {
      search (newValue) {
        if (this.value.valueType === 'name') {
          this.searchQuery = newValue
          return
        }

        this.searchQuery = newValue

        if (!this.$data.localFocused) {
          return []
        }

        if (newValue.trim()) {
          this.searchSelect = 0
          this.$data.searchOptions = this.options.filter((option) => {
            const queryValue = newValue.toLowerCase()

            if (option.match) {
              return option.match(queryValue)
            }

            const optionValue = option.label.toLowerCase()
            return optionValue.includes(queryValue)
          })
        } else {
          this.$data.searchOptions = this.options
        }
      },

      setOptions (scope = this.scope) {
        this.options = pipe(
          filter((v) => typeMatches(v, this.value.valueType)),
          filter((v) => !v.filter || v.filter(this.action)),
          map(prop('atoms')),
          flatten
        )(scope)
      },

      receiveKeys (e) {
        const handle = keyBehaviour[e.keyCode]

        if (handle != null) {
          if (e.keyCode !== codes.backspace) { e.preventDefault() }
          handle(this, e)
        }
      },

      focus () {
        this.localFocused = true
        this.$data.searchOptions = this.options
        this.searchSelect = 0
      },

      blur (e) {
        if (this.value.valueType === 'name') {
          if (this.searchQuery.length > 0) {
            this.$emit('input', {
              id: this.searchQuery,
              type: 'atom',
              label: this.searchQuery
            })
          }
        }

        this.localFocused = false
        this.$data.searchOptions = []
        this.searchQuery = ''
      },

      optionHover (index) {
        this.$data.searchSelect = index
      },

      optionClick (index) {
        this.focus()
        this.$data.searchSelect = index
        this.selectCurrent()
      },

      selectCurrent () {
        if (this.value.valueType === 'name') {
          this.$emit('input', {
            id: this.searchQuery,
            type: 'atom',
            label: this.searchQuery
          })
          return
        }

        let current = this.$data.searchOptions[this.$data.searchSelect]

        const description = atoms[current.id]
        if (description && description.type === 'function') {
          current = {
            type: 'atom-list',
            expressions: [
              current,
              ...map((valueType) => ({type: 'sexp-hole', valueType}), description.parameters)
            ]
          }
        }

        this.$emit('input', current)
      }
    }
  })
</script>

<style scoped lang="scss">
  .source-select {
    position: relative;
    display: inline-block;
    width: 100%;
  }

  .source-select.rest {
    background-color: #000;
  }

  .placeholder {
    position: absolute;
    top: 0;
    left: 8px;
    z-index: 4;
    pointer-events: none;
    line-height: 32px;
    font-size: 16px;
    opacity: 0.5
  }

  .search {
    padding: 0 8px;
    background-color: transparent;
    border: none;
    outline: none;
    width: auto;
    display: inline-block;
    width: 100%;
    min-width: 30px;
    width: 100%;
    font-size: 16px;
    line-height: 32px;
    z-index: 5;
  }

  .search-options {
    margin: 0;
    min-width: 200px;
    padding: 0;
    list-style: none;
    border: 0.5px solid #ddd;
    border-radius: 2px;
    width: auto;
    display: inline-block;
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 100;
    background-color: #f3f3f3;

    & > li {
      padding: 8px 8px;
      width: auto;
      display: block;

      &.selected {
        background-color: #ddd;
      }
    }
  }
</style>


