<template>
  <div>
    <modal v-model="deleting">
      Are you sure you want to permanently destroy this process?
      <div slot="modal-footer" class="modal-footer">
        <button
          type="button"
          class="btn btn-danger"
          @click="deleteSelf()">Destroy</button>

        <button
          type="button"
          class="btn btn-primary"
          @click="deleting = false">Cancel</button>
      </div>
    </modal>

    <div class="row">
      <div class="header">
        <input class="name-input title-input" v-model="processName" />
        <div>
          <button class="btn btn-primary" @click="saveSelf()" >Save</button>
          <button class="btn btn-danger" @click="deleting = true">
            <i class="fa fa-trash"></i>
          </button>
        </div>
      </div>
    </div>

    <modal v-model="editingRole">
      <div class="form-group">
        <label>Role</label>
        <input v-model="roleName" type="text" class="form-control" />
      </div>
      <div slot="modal-footer" class="modal-footer">
        <button
          type="button"
          class="btn btn-danger"
          @click="destroyRole()">Destroy</button>

        <button
          type="button"
          class="btn btn-primary"
          @click="saveEditRole()">Save</button>
      </div>
    </modal>

    <div class="row">
      <div class="roles well col-12">
        <div class="role-header">
          <h4 class="role-title">Roles</h4>
          <div>
            <button class="btn add-role" @click="createRole()"><i class="fa fa-plus"></i></button>
          </div>
        </div>
        <div class="tokens">
          <div v-for="role in process.roles" class="token" @click="editRole(role)">
            {{ role.name }}
          </div>
        </div>
      </div>
    </div>

    <div class="steps row">
      <button class="btn btn-default add-step" v-on:click="addStep()">
        <i class="fa fa-plus"></i> Step</a>
      </button>
      <accordion class="step" v-for="step in process.steps" :key="step.id">
        <build-step :step="step"></build-step>
      </accordion>
    </div>
  </div>
</template>

<script>
  import {mapMutations, mapActions, mapGetters} from 'vuex'
  import BuildStep from './BuildStep.vue'
  import {modal, accordion} from 'vue-strap'
  import Panel from './Panel'

  export default {
    components: {
      BuildStep,
      Panel,
      modal,
      accordion
    },

    data () {
      return {
        processName: this.$store.getters.process.name,
        deleting: false,
        roleName: null,
        roleId: null,
        editingRole: false
      }
    },

    computed: {
      ...mapGetters([
        'process'
      ])
    },

    watch: {
      processName (newValue) {
        this.setProcessName({id: this.process.id, name: newValue})
      }
    },

    methods: {
      ...mapMutations([
        'addStep',
        'setProcessName',
        'addRole',
        'removeRole',
        'saveRole'
      ]),

      ...mapActions([
        'deleteProcess',
        'saveProcess'
      ]),

      saveSelf () {
        this.saveProcess({process: this.process})
      },

      deleteSelf () {
        this.deleteProcess({processId: this.process._id})
      },

      saveEditRole () {
        this.saveRole({name: this.roleName, id: this.roleId})
        this.editingRole = false
      },

      editRole (role) {
        this.roleName = role.name
        this.editingRole = true
        this.roleId = role.id
      },

      destroyRole () {
        this.removeRole({id: this.roleId})
        this.editingRole = false
      },

      createRole (id) {
        this.addRole({name: ''})
        this.editRole(this.process.roles[this.process.roles.length - 1])
      }
    }
  }
</script>

<style scoped>
  .name-input {
    font-size: 2em;
  }

  .step {
    margin-top: 16px;
  }

  .role-header {
    display: flex;
    margin-bottom: 16px;
    align-items: center;
  }

  .role-title {
    margin: 0;
  }

  .add-role {
    padding: 0;
    background-color: transparent;
    border: none;
    margin-left: 8px;
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
</style>


