
<template>
  <div>
    <div class="row panel panel-default">
      <div class="panel-body">
        <ul class="nav nav-pills col-sm-10">
          <li v-for="childProcess in processes" :class="{active: processId === childProcess._id}">
            <a v-on:click="openProcess({processId: childProcess._id})">{{ childProcess.name }}</a>
          </li>
        </ul>
        <button class="btn btn-default pull-right" v-on:click="createProcess()">
          <i class="fa fa-plus"></i> Process</a>
        </button>
      </div>
    </div>
    <div>
      <build-process v-if="process" :key="processId"></build-process>
      <alert v-else type="info" class="row">Select or add a process to get started!</alert>
    </div>
  </div>
</template>

<script>
  import {mapMutations, mapActions, mapGetters} from 'vuex'
  import Panel from './Panel'
  import BuildProcess from './BuildProcess'
  import {alert} from 'vue-strap'

  export default {
    name: 'processes',

    computed: {
      ...mapGetters([
        'process'
      ]),

      processes () {
        return this.$store.state.processes
      },

      processId () {
        return this.process && this.process._id
      }
    },

    created () {
      this.loadProcesses()
      this.loadForms()
    },

    methods: {
      ...mapMutations([
        'setCurrentProcess'
      ]),

      ...mapActions([
        'loadProcesses',
        'loadForms',
        'createProcess',
        'openProcess'
      ])
    },

    components: {
      Panel,
      BuildProcess,
      alert
    }
  }
</script>

<style></style>



