
<template>
  <div :class="['panel',panelType]">
    <div :class="['panel-heading', 'panel-heading-flex']">
      <i :class="['fa', 'accordion-toggle', open ? 'fa-minus' : 'fa-plus']" @click.prevent="toggle()"></i>
      <slot name="header"><h4 class="panel-title">{{ header }}</h4></slot>
    </div>
    <transition
      name="collapse"
      @enter="enter"
      @after-enter="afterEnter"
      @before-leave="beforeLeave"
    >
      <div class="panel-collapse" v-if="open">
        <div class="panel-body">
          <slot></slot>
        </div>
        <div :class="['panel-footer']">
          <slot name="footer"></slot>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props: {
    header: {type: String},
    isOpen: {type: Boolean, default: null},
    type: {type: String, default: null}
  },
  data () {
    return {
      open: this.isOpen
    }
  },
  watch: {
    isOpen (val) {
      this.open = val
    }
  },
  computed: {
    inAccordion () { return this.$parent && this.$parent._isAccordion },
    panelType () { return 'panel-' + (this.type || (this.$parent && this.$parent.type) || 'default') }
  },
  methods: {
    toggle () {
      if (this.isOpen != null) {
        this.$emit('state', !this.open)
      } else {
        this.open = !this.open
      }
    },
    enter (el) {
      el.style.height = 'auto'
      var endWidth = window.getComputedStyle(el).height
      el.style.height = '0px'
      el.offsetHeight
      el.style.height = endWidth
    },
    afterEnter (el) {
      el.style.height = 'auto'
    },
    beforeLeave (el) {
      el.style.height = window.getComputedStyle(el).height
      el.offsetHeight // force repaint
      el.style.height = '0px'
    }
  },
  created () {
    if (this.isOpen === null) {
      this.open = !this.inAccordion
    }
  }
}
</script>

<style>
.accordion-toggle {
  cursor: pointer;
  padding-right: 15px;
  display: flex;
  align-items: center;
  color: rgba(0, 0, 0, 0.5)
}

.panel-heading-flex {
  display: flex;
}

.panel-heading-flex > *:nth-child(2) {
  flex-grow: 1;
}

.collapse-enter-active,
.collapse-leave-active {
  transition: all .5s ease;
  overflow: hidden;
}
.collapse-enter,
.collapse-leave-active {
}
</style>


