<template>
  <component :focused="focused" :action="action" :type="type" :scope="scope" @input="setValue(arguments[0])" :value="value" v-bind:is="value.type" />
</template>

<script>
  export default {
    props: {
      value: {
        type: Object
      },

      scope: {
        type: Array,
        required: true
      },

      action: {
        type: Object,
        required: true
      },

      type: String,

      focused: Boolean
    },

    data () {
      return {
        position: 0,
        hovered: false,
        options: [],
        sexp: this.value
      }
    },

    methods: {
      setValue (value) {
        this.$emit('input', value)
      }
    }
  }
</script>



