import {values, view, lensPath, any, compose, equals} from 'ramda'

export const types = {
  form: 'form',
  future: 'result',
  request: 'request',
  dependency: 'dependency',
  reciever: 'reciever',
  role: 'role',
  name: 'name'
}

const typeId = lensPath(['tree', 'expressions', 0, 'id'])

export const isMul = (action) => {
  return any(compose(equals('each'), view(typeId)), action.props.conditions)
}

export const atoms = {
  exists: {
    id: 'exists',
    type: 'function',
    atoms: [{
      type: 'atom',
      id: 'exists',
      label: 'filled',
      sexpType: 'unary'
    }],
    parameters: [types.future],
    returnType: types.dependency
  },

  each: {
    id: 'each',
    type: 'function',
    atoms: [{
      type: 'atom',
      id: 'each',
      label: 'for each',
      sexpType: 'unary'
    }],
    parameters: [types.future],
    returnType: types.dependency
  },

  approve: {
    id: 'approve',
    type: 'function',
    buildResults: ([{label}], action, step) => {
      const approvedId = label
      const approved = {
        id: approvedId,
        type: 'approval',
        actionId: action.id,
        stepId: step.id,
        atoms: [{
          type: 'atom',
          actionId: action.id,
          stepId: step.id,
          id: approvedId,
          mul: action.multiplePackages || action.type === 'mul',
          label: label,
          sexpType: 'value'
        }],
        returnType: types.future
      }

      return [approved]
    },
    atoms: [{
      type: 'atom',
      id: 'approve',
      label: 'approve',
      sexpType: 'binary'
    }],
    parameters: [types.name, types.future],
    returnType: types.request
  },

  form: {
    id: 'form',
    type: 'function',
    buildResults: ([{label}], action, step) => {
      return [{
        id: label,
        type: 'submission',
        actionId: action.id,
        stepId: step.id,
        atoms: [{
          type: 'atom',
          id: label,
          actionId: action.id,
          stepId: step.id,
          mul: action.multiplePackages || action.type === 'mul',
          label: label,
          sexpType: 'value'
        }],
        returnType: types.future
      }]
    },
    atoms: [{
      type: 'atom',
      id: 'form',
      label: 'form',
      sexpType: 'binary'
    }],
    parameters: [types.name, types.form],
    returnType: types.request
  },

  submission: {
    id: 'submission',
    type: 'function',
    atoms: [{
      type: 'atom',
      id: 'submission',
      label: 'submission',
      sexpType: 'binary'
    }],
    parameters: [types.name, types.future],
    returnType: types.request
  },

  all: {
    id: 'all',
    type: 'function',
    atoms: [{
      type: 'atom',
      id: 'all',
      label: 'every',
      sexpType: 'unary'
    }],
    parameters: [types.role],
    returnType: types.reciever
  },

  this: {
    id: 'this',
    type: 'function',
    filter: (action) => {
      return isMul(action)
    },
    atoms: [{
      type: 'atom',
      id: 'this',
      label: 'this',
      sexpType: 'unary'
    }],
    parameters: [types.role],
    returnType: types.reciever
  },

  thisSubmission: {
    id: 'thisSubmission',
    filter: (action) => {
      return isMul(action)
    },
    atoms: [{
      type: 'atom',
      id: 'thisSubmission',
      label: 'this submission',
      sexpType: 'value'
    }],
    returnType: types.future
  }

}

export const atomList = values(atoms)

export const typeMatches = (value, type) => {
  return value.returnType === type
}



