// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Sortable from 'sortablejs'
import getStore from './stores/process'
import {focus} from 'vue-focus'
import {types} from './sexp'

// Load s-exp types
import './components/Atom'
import './components/AtomList'
import './components/SexpHole'

Vue.directive('focus', focus)

/* eslint-disable no-new */
Vue.directive('sortable', {
  inserted: function (el, binding) {
    new Sortable(el, binding.value || {})
  }
})

export default App

window.mountProcessBuilder = (selector, forms) => {
  new Vue({
    store: getStore(forms),
    el: selector,
    render: (h) => h(App)
  })
}

if (window.NODE_ENV !== 'production') {
  window.mountProcessBuilder('#app', [{id: 'ex-form', type: 'form', returnType: types.form, atoms: [{type: 'atom', sexpType: 'value', id: 'ex-form', label: 'Ex Form'}]}])
}



