<template>
  <div @dblclick="empty()" :class="['atom', itemClass]">
    <i class="fa fa-exclamation-circle" v-if="!inScope"></i> {{ value.label }}
  </div>
</template>

<script>
  import Vue from 'vue'

  export default Vue.component('atom', {
    props: {
      value: {
        type: Object,
        required: true
      },

      scope: {
        type: Array
      },

      type: String
    },

    computed: {
      itemClass () {
        return this.value.atomType || ''
      },

      inScope () {
        if (this.scope && this.value.sexpType === 'value') {
          return this.scope.some((v) =>
            v.id === this.value.id &&
            v.stepId === this.value.stepId &&
            v.actionId === this.value.actionId
          )
        }

        return true
      }
    },

    methods: {
      empty () {
        if (this.type) {
          this.$emit('input', {type: 'sexp-hole', valueType: this.type})
        }
      }
    }
  })
</script>

<style scoped>
.atom {
  display: inline-block;
}
</style>


