
import Vuex from 'vuex'
import {
  find,
  compose,
  equals,
  prop,
  map,
  flatten,
  curry,
  append,
  findIndex,
  remove,
  update,
  propEq,
  concat,
  all,
  pluck
} from 'ramda'
import uuid from 'uuid'
import Vue from 'vue'
import feathers from 'feathers-client'
import io from 'socket.io-client'
import { types, atomList, atoms } from '../sexp'

Vue.use(Vuex)

const socket = io('http://connect.intelligen.energy:3030')
const app = feathers()
  .configure(feathers.socketio(socket))
  .configure(feathers.hooks())

const state = (forms) => {
  return {
    processes: [],
    process: null,
    scope: [],
    forms
  }
}

export const log = curry((a, b) => {
  console.log(a, b)
  return b
})

const get = (list, processId) => {
  return find(compose(equals(processId), prop('id')), list)
}

const getResults = ({tree}, action, step) => {
  if (tree.expressions != null && tree.expressions && all((v) => v.type !== 'sexp-hole', tree.expressions)) {
    const {expressions: [head, ...tail]} = tree
    const buildResults = atoms[head.id].buildResults
    if (buildResults instanceof Function) {
      return buildResults(tail, action, step)
    }
  }

  return []
}

export const getActionResults = (step, action) => {
  const results = compose(
    flatten,
    map((p) => getResults(p, action, step)),
    prop('package'),
    prop('props')
  )(action)

  action.props.results = flatten(pluck('atoms', results))

  return results.map((result) => ({
    id: result.id,
    stepId: step.id,
    actionId: action.id,
    type: 'result',
    atoms: [{
      type: 'atom',
      stepId: step.id,
      actionId: action.id,
      id: result.id,
      label: result.atoms[0].label,
      sexpType: 'value'
    }],
    returnType: types.future
  }))
}

const getStepResults = (step) => compose(map((action) => getActionResults(step, action)), prop('actions'))(step)
const getProcessResults = compose(map(getStepResults), prop('steps'))
const createScope = (process, forms) => {
  const base = atomList
  const results = flatten(getProcessResults(process))
  const roles = process.roles || []

  return concat(roles, concat(base, concat(forms, results)))
}

const getters = {
  process (state) {
    return state.process
  }
}

const buildRole = (id, name) => {
  return { id, name, returnType: types.role, atoms: [{ type: 'atom', label: name, id, sexpType: 'role' }] }
}

const toFormAtoms = (form) => {
  return {
    id: form._id,
    type: types.form,
    returnType: types.form,
    atoms: [{type: 'atom', sexpType: 'value', id: form._id, label: form.name}]
  }
}

const mutations = {
  setProcesses (state, {processes}) {
    state.processes = processes

    if (state.process && find(propEq('_id', state.process._id), state.processes) == null) {
      mutations.setProcess(state, { process: null })
    }
  },

  setForms (state, {forms}) {
    state.forms = map(toFormAtoms, forms)

    if (state.process) {
      state.scope = createScope(state.process, state.forms)
    }
  },

  setProcess (state, { process }) {
    state.process = process

    if (process) {
      state.scope = createScope(state.process, state.forms)
    }
  },

  addRole (state, { name }) {
    if (!Array.isArray(state.process.roles)) {
      state.process.roles = []
    }
    state.process.roles.push(buildRole(uuid.v4(), name))
    state.scope = createScope(state.process, state.forms)
  },

  removeRole (state, {id}) {
    const index = findIndex(propEq('id', id), state.process.roles)
    state.process.roles.splice(index, 1)
    state.scope = createScope(state.process, state.forms)
  },

  saveRole (state, { id, name }) {
    const index = findIndex(propEq('id', id), state.process.roles)
    state.process.roles.splice(index, 1, buildRole(id, name))
    state.scope = createScope(state.process, state.forms)
  },

  addStep (state) {
    getters.process(state).steps.push({
      id: uuid.v4(),
      name: '',
      actions: [],
      conditionAction: {type: 'single', props: {conditions: []}}
    })
  },

  removeStep (state, {stepId}) {
    const index = findIndex(propEq('id', stepId), state.process.steps)
    getters.process(state).steps.splice(index, 1)
  },

  addAction (state, { stepId }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)

    step.actions.push({
      id: uuid.v4(),
      name: '',
      type: 'single',
      props: null
    })
  },

  setProcessName (state, { name }) {
    getters.process(state).name = name
  },

  setStepName (state, { stepId, name }) {
    const process = getters.process(state)
    get(process.steps, stepId).name = name
  },

  setStepConditionProps (state, { stepId, props }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)

    if (!step.conditionAction) {
      step.conditionAction = {}
    }

    step.conditionAction.props = props
  },

  setActionName (state, { stepId, actionId, name }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)
    const action = get(step.actions, actionId)

    action.name = name
  },

  removeAction (state, { stepId, actionId }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)
    const index = findIndex(propEq('_id', actionId), step.actions)

    step.actions.splice(index, 1)
  },

  setActionType (state, { stepId, actionId, type }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)
    const action = get(step.actions, actionId)
    action.type = type

    state.scope = createScope(process, state.forms)
  },

  setActionMultiplePackages (state, { stepId, actionId, multiplePackages }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)
    const action = get(step.actions, actionId)
    action.multiplePackages = multiplePackages

    state.scope = createScope(process, state.forms)
  },

  setActionProps (state, { stepId, actionId, props }) {
    const process = getters.process(state)
    const step = get(process.steps, stepId)
    const action = get(step.actions, actionId)
    action.props = props

    state.scope = createScope(process, state.forms)
  }
}

const processService = app.service('processes')
const formsService = app.service('forms')

let store
processService.on('created', (newProcess) => {
  store.commit('setProcesses', { processes: append(newProcess, store.state.processes) })
})

processService.on('updated', (updated) => {
  const index = findIndex(({_id}) => updated._id === _id, store.state.processes)
  store.commit('setProcesses', { processes: update(index, updated, store.state.processes) })
})

processService.on('removed', (removed) => {
  const index = findIndex(({_id}) => removed._id === _id, store.state.processes)
  store.commit('setProcesses', { processes: remove(index, 1, store.state.processes) })
})

const actions = {
  async saveProcess ({commit, state}, {process}) {
    const savedProcess = await processService.update(process._id, process)
    commit('setProcess', { process: savedProcess })
  },

  async deleteProcess ({commit, state}, {processId}) {
    await processService.remove(processId)
  },

  async createProcess ({commit, state}) {
    const newProcess = await processService.create({
      name: 'New Process',
      steps: [],
      roles: []
    })

    commit('setProcess', { process: newProcess })
  },

  async openProcess ({commit, state}, {processId}) {
    const process = await processService.get(processId)

    commit('setProcess', { process })
  },

  async loadForms ({commit, state}) {
    const response = await formsService.find({ query: { published: {$ne: null} } })

    commit('setForms', { forms: response.data })
  },

  async loadProcesses ({commit, state}) {
    const response = await processService.find({})

    commit('setProcesses', { processes: response.data })
  }
}

export default (forms) => {
  store = new Vuex.Store({
    state: state(forms),
    mutations,
    getters,
    actions
  })

  return store
}



