<template>
  <panel @state="isOpen = arguments[0]" :isOpen="isOpen">
    <modal v-model="removing">
      Are you sure you want to permanently destroy this step?
      <div slot="modal-footer" class="modal-footer">
        <button type="button" class="btn btn-danger" @click="removeSelf()">Destroy</button>

        <button type="button" class="btn btn-primary" @click="removing = false">Cancel</button>
      </div>
    </modal>

    <div slot="header" class="header">
      <input placeholder="Step Title" class="title-input name-input" v-model="stepName" />
      <div>
        <button v-if="isOpen" class="btn btn-primary add-action" v-on:click="addAction({stepId: step.id})">
          <i class="fa fa-plus"></i> Action
        </button>
        <!-- <button class="btn btn-default"><i class="fa fa-arrows"></i></button> -->
        <button v-if="isOpen" class="btn btn-default" @click="step.actions.length > 0 ? removing = true : removeSelf()"><i class="fa fa-trash"></i></button>
      </div>
    </div>

    <div class="actions">
      <build-action v-for="action in step.actions" :action="action" :step="step"></build-action>
    </div>

    <div slot="footer" class="finishWhen sexp-box">
      <div class="sexp-label">
        <label>Finish When</label>
        <button class="btn-icon add-sexp" v-on:click="addCondition()">
            <i class="fa fa-plus"></i>
          </button>
      </div>
      <div class="sexp">
        <div v-if="step.conditionAction.props.conditions.length === 0" class="placeholder">
          Never
        </div>
        <div v-else v-for="(condition, index) in step.conditionAction.props.conditions" class="tree">
          <sexp @input="setTree('conditions', index, arguments[0])"
                :value="condition.tree"
                :action="step.conditionAction"
                :scope="scope"
                :focused="true" />
        </div>
      </div>
    </div>
  </panel>
</template>

<script>
  import { mapMutations } from 'vuex'
  import BuildAction from './BuildAction'
  import { modal } from 'vue-strap'
  import Sexp from './Sexp'
  import panel from './Panel'
  import {over, append, lensProp, lensPath, remove, set} from 'ramda'
  import {types} from '../sexp'

  const appendToChild = (prop, value, object) => {
    return over(lensProp(prop), append(value), object)
  }

  export default {
    props: {
      step: {
        type: Object,
        required: true
      }
    },

    components: {
      panel,
      BuildAction,
      modal,
      Sexp
    },

    data () {
      return {
        stepName: this.step.name,
        removing: false,
        isOpen: true
      }
    },

    computed: {
      scope () {
        return this.$store.state.scope
      }
    },

    watch: {
      stepName (newValue) {
        this.setStepName({ stepId: this.step.id, name: newValue })
      }
    },

    methods: {
      ...mapMutations([
        'setStepName',
        'addAction',
        'removeStep',
        'setStepConditionProps'
      ]),

      removeSelf () {
        this.removeStep({ stepId: this.step.id })
      },

      setProps (newProps) {
        this.setStepConditionProps({stepId: this.step.id, props: newProps})
      },

      addCondition () {
        this.setProps(appendToChild(
          'conditions',
          {tree: {type: 'sexp-hole', valueType: types.dependency}},
          this.step.conditionAction.props
        ))
      },

      setTree (type, index, newTree) {
        if (newTree == null) {
          this.setProps(over(lensProp(type), remove(index, 1), this.step.conditionAction.props))
          return
        }

        this.setProps(set(lensPath([type, index, 'tree']), newTree, this.step.conditionAction.props))
      }
    }
  }
</script>

<style scoped>
.name-input {
  font-size: 1.5em;
}

.actions {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: -8px;
}

.actions > * {
  margin: 8px;
}

.panel-footer {
  margin: -15px;
  margin-top: 16px;
}

.tree {
  margin-bottom: 16px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>


